import React from 'react';


class JsPage extends React.Component {
    constructor(props) {
        super(props);

        const today = new Date();

        const thisMonth=6;

        const holidays_06 = new Array(2,4,7,12,15,18,20,21,22,23,25) ;
        const holidays_07 = new Array(2,7,9,14,16,19,23,27,30) ;


        this.state={
            msg:'新闻',
            list:[holidays_06,holidays_07]
        }
    }



    render() {

        let listResult=this.state.list.map(function(value,key){
            return (<li key={key}>{value}</li>)
        })
        return (
                <div>
                    {listResult}
                </div>


        );
    }
}

export default JsPage;


